@import "src/common/styles/variables/variables.scss";

/* GLOBAL STYLES */
html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

html,
body {
  width: 100%;
  height: 100%;
  position: relative;
}

html.lock,
body.lock {
  overflow: hidden;
}

body {
  font-family: 'Poppins', sans-serif;
  font-size: 15px;
  font-weight: 500;
  line-height: 1.6;
  color: #666;
  overflow-x: hidden;
  min-width: 320px;
}

a {
  display: inline-block;
  text-decoration: none;
  color: black;
}

ul,
li {
  margin: 0;
  padding: 0;
  list-style: none;
}


.burgerMenuBlock {
  display: none;
  position: relative;
  background-color: $elementsBc;
  width: 100%;
  height: 62px;
}

@media (max-width: $tablets) {
  .burgerMenuBlock {
    display: flex;
    align-items: center;
  }
}


/* END GLOBAL STYLES */